
import XHeader from 'vux/src/components/x-header/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import { setStore, getStore } from '../../config/mUtils'
import { getTiding, delTidingById } from '../../service/getData'
export default {
components: {
XHeader,
Sticky,
Divider,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Confirm
},
data () {
return {
title: '我的消息',
isempty:true,
isread:false,
show:false,
list:[],
type:0,
index:0,
id:0
}
},
mounted () {
this.$nextTick(() => {
this.$vux.loading.show()
})

getTiding(getStore('userId')).then(resolved => {
if(resolved.msgCode == '0000') {
if(resolved.data.length === 0) {
this.isempty = true
}
this.list = resolved.data
console.log(JSON.stringify(resolved.data))
this.$nextTick(() => {
this.isempty = false
this.$vux.loading.hide()
})
} else {
this.$vux.toast.show({
type:'text',
text:resolved.msg,
position:'bottom'
})
}
})
},
methods:{
openRouter(path){
this.$router.push(path)
},
onConfirm(){
delTidingById(this.id).then((res)=>{
if(res.msgCode==='0000'){
this.list.splice(this.index,1)
this.$vux.toast.show({
text:'删除成功',
type:'text',
position:'bottom'
})
}
})
},
onButtonClick(id,index){
console.log(id)
this.id = id
this.index = index
this.show = true
}
}
}



